*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  
}

.center{
  max-width: 960px;
  margin: 0 auto;
  padding: 4% 0 0 2%;
  display: flex;
  flex-wrap: wrap;
}

.main{
  overflow: hidden;
  padding-left: 40px 0;
  background-color: blueviolet;
  min-height: 800px;
  background-image: url('https://cursos.dankicode.com/app/Views/public/images/home/banner-home.png');
  background-position: center;
  background-size: cover;
}
.menu{
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.menu h3{
  color: white;
}
.menu a{
  color: white;
  text-decoration: none;
  border: 1px solid white;
  padding: 3px 8px;
  border-radius: 5px;
}
.menu a:hover{
  background-color: #e7426b;
  transition: 1s;
}
.form{
  display: flex;
  width: 100%;
  max-width: 600px;
  flex-wrap: wrap;
  margin: 0 auto;
  justify-content: center;
  margin: 150px auto;
}
.form h2{
  color: #e7426b;
  text-transform: uppercase;
  font-weight: normal;
  font-size: 28px;
}

.items-form{
  margin: 0 auto;
  max-width: 540px ;
  display: flex;
  width: 100%;
  justify-content: space-around;
  
  flex-wrap: wrap;
}
.items-form:focus{
  border: 3px solid #e7426b;
}
.items-form input{
  width: 48%;
  border: 2px solid #e7426b;
  height: 30px;
  margin: 8px 0px;
  padding-left: 10px;
}
.items-form input[type=submit]{
  width: 100%;
  height: 40px;
  border: none;
  background-color: #e7426b;
  cursor: pointer;
  color: white;
  font-size: 19px;
}

@media screen and (max-width: 768px){
  .items-form input[type=text]{
    width: 100%;
    padding-left: 10px;
  }
}